import Vue from "vue";
import '@/assets/patch.css'
import '@/assets/css/video.css'
import './assets/css/app.css'
import './assets/css/extra.css'
import './assets/css/ruby.dark.css'
import './assets/css/ruby_china.css'
import './assets/css/forum.css'
import './assets/css/dark.theme.css'
import './assets/css/theme.css'
import './assets/css/golbal.css'
import './assets/css/vuetifyjs.extra.css'
import './assets/mobile/app.css'


import app__dialog from "@/components/ads/app__dialog";

export default {
  name: 'App',
  components: {
    app__dialog
  },
  data() {
    return {
      header_show: true,
      footer_show: true,
      header_margin_top: 0,
      app_conf: undefined,
      is_support_webp: false
    }
  },
  created() {

    const FastClick = require('fastclick');
    FastClick.attach(document.body);

    //dark 模式
    this.$vuetify.theme.dark = true;
    this.$q.dark.set(true);

    window.app = this;
    Vue.prototype.$app = this;

    this.$nextTick(() => {
      this.init_header_height();
      this.show_app_dialog();
      this.is_support_webp = this.isSupportWebp();
    });
  },
  methods: {
    isSupportWebp: function () {
      try {
        return document.createElement('canvas').toDataURL('image/webp', 0.5).indexOf('data:image/webp') === 0;
      } catch (err) {
        return false;
      }
    },
    init_header_height: function () {
      if (this.$refs.appheader) {
        if (!this.$refs.appheader.header_height || this.$refs.appheader.header_height < 1) {
          return setTimeout(this.init_header_height, 100);
        }
      }
      try {
        console.log('appheader height:', this.$refs.appheader.header_height);
        this.header_margin_top = this.$refs.appheader.header_height + 5;
      } catch (e) {
        this.header_margin_top = 0;
      }

      if (this.header_margin_top == 0) {
        this.header_margin_top = 70;
        if (this.is_mobile()) {
          this.header_margin_top += 30;
        }
      }
      console.log('header_margin_top:', this.header_margin_top);
    },
    //是否显示头部
    header: function (bool) {
      this.header_show = bool;
    },
    //是否显示底部
    footer: function (bool) {
      this.footer_show = bool;
    },
    show_app_dialog: function () {

      var now_time = (new Date()).valueOf();
      var last_show_time = parseFloat(localStorage.getItem("app__dialog__show_time"));
      var url_vars = this.getUrlVars();
      var must_show = false;
      if (url_vars.f) {
        must_show = true;
      }

      if (navigator.userAgent.indexOf('bot') < 0) {
        if (!this.is_ios() && !this.is_bot()) {
          if (must_show || isNaN(last_show_time) || now_time - last_show_time > 1000 * 60 * 60 * 6) {
            console.log('this.$ads_object:', this.$ads_object);
            var x91app_conf = this.$ads_object.text['x91.18r'];
            this.app_conf = x91app_conf;
            localStorage.setItem("app__dialog__show_time", now_time);
          }
        }
      }

    }
  }
}
