export default {
  name: "app__dialog",
  props: {
    app_conf: undefined,
  },
  data() {
    return {
      dialog: true,
    }
  },
  created() {

  },
  methods: {
    on_download: function () {
      this.navigateToUrl(this.app_conf.url);
    }
  },
  computed: {
    app_download_url: function () {
      var f_p = 'app__dialog';
      var fs = this.get_from_source();
      if (fs) {
        f_p = f_p + ":" + fs;
      }
      return this.updateQueryStringParameter(this.app_conf.url, 'f', f_p);
    }
  }
}
