import Vue from 'vue'
import VueAnalytics from 'vue-analytics'
import * as Sentry from "@sentry/vue";
import { Integrations } from "@sentry/tracing";

import './quasar'
import vuetify from './plugins/vuetify';

import './modules/app'
import './modules/common'
import './modules/requests'
import './modules/forum'
import './modules/ads'
import './modules/traffic'
import './modules/lang'
import './modules/video'
import store from './modules/store'
import flutter from './modules/flutter'
import base from './modules/base'
import './modules/js.extend'

Vue.use(flutter)
Vue.use(base)

import VueLazyload from 'vue-lazyload'

Vue.use(VueLazyload, {
    preLoad: 1.1,
    attempt: 1
});

import router from './router'
import item_tip from './components/items/tip'
import topic_item from './components/forum/items/topic_item'
import video_item from './components/video/item/video_item'
import header from './components/header'
import footer from './components/footer'
import tags from './components/common/tags'
import video_list from '@/components/common/video_list'

Sentry.init({
  Vue,
  dsn: "https://15eed1acff1f4ae2b15cf3e60344d3d7@o965611.ingest.sentry.io/5916514",
  integrations: [
    new Integrations.BrowserTracing({
      routingInstrumentation: Sentry.vueRouterInstrumentation(router),
      tracingOrigins: ["localhost", "my-site-url.com", /^\//],
    }),
  ],
  // Set tracesSampleRate to 1.0 to capture 100%
  // of transactions for performance monitoring.
  // We recommend adjusting this value in production
  tracesSampleRate: 1.0,
});


Vue.config.productionTip = false

Vue.component('tags', tags)
Vue.component('video_list', video_list)
Vue.component('video_item', video_item)
Vue.component('item_tip', item_tip)
Vue.component('appheader', header)
Vue.component('appfooter', footer)
Vue.component('topic_item', topic_item)

import App from './App.vue'


window.gapp = new Vue({
    render: h => h(App),
    vuetify,
    router: router,
    store,
    created() {
        if (this.$web_conf.analytics) {
            Vue.use(VueAnalytics, {
                id: this.$web_conf.analytics['google'],
                router,
                autoTracking: {
                    pageviewOnLoad: false
                }
            });
        }
        this.$store.dispatch('init');
        this.init();
    }
}).$mount('#app')


